"use client";
import React, { useEffect, useRef } from "react";
import { getReactSDK } from "firebaseui";
import { auth } from "../firebase"; // Import your Firebase instance
import firebase from "firebase/compat/app";
import "firebase/compat/auth"; // Import necessary auth modules
import "firebaseui/dist/firebaseui.css";

interface FirebaseUIWidgetProps {
  uiConfig: firebaseui.auth.Config;
}

const FirebaseUIWidget: React.FC<FirebaseUIWidgetProps> = ({ uiConfig }) => {
  const firebaseUiContainer = useRef<HTMLDivElement | null>(null);
  const [uiInstance, setUiInstance] =
    React.useState<firebaseui.auth.AuthUI | null>(null);

  useEffect(() => {
    if (firebaseUiContainer.current) {
      try {
        const instance = getReactSDK(firebase, auth, uiConfig).renderReact(
          firebaseUiContainer.current,
        );
        setUiInstance(instance);
        return () => {
          if (instance) {
            instance.delete();
          }
        };
      } catch (error) {
        console.error("Error initializing FirebaseUI:", error);
      }
    }
  }, [uiConfig]);

  return <div ref={firebaseUiContainer} />;
};

export default FirebaseUIWidget;
